function Maintop() {
    return (
      <>

  <section className="brand">
    <div className="brandbox">
      <ul className="aqua1"><a href="#"><img src={process.env.PUBLIC_URL+"/img/section1.jpg"}/></a></ul>
      <ul className="aqua2"><a href="#"><img src={process.env.PUBLIC_URL+"/img/section2.jpg"}/></a></ul>
      <ul className="brand-logo">
        <img className="brandlogo" src={process.env.PUBLIC_URL+"/img/brandlogo.png" }/>
        <a>Story</a>
      </ul>
      <ul className="ingri">
        <li className="ingri1"><img src={process.env.PUBLIC_URL+"/img/effect-s.png"}/></li>
        <li className="ingri2 delay-05s"><img src={process.env.PUBLIC_URL+"/img/effect2.png" }/></li>
        <li className="ingri3 delay-1s"><img src={process.env.PUBLIC_URL+"/img/effect0.png" }/></li>
        <li className="ingri4 delay-15s "><img src={process.env.PUBLIC_URL+"/img/effect-a.png" }/></li>
        <li className="ingri5 delay-2s "><img src={process.env.PUBLIC_URL+"/img/effect-ci.png" }/></li>
      </ul>
    </div>
  </section>


</>
    );
  }
  
  export default Maintop;
  