// import Swiper core and required modules
import { Pagination, A11y, Autoplay } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import '../App.css'
import '../torriden'
import styled from 'styled-components'



export default () => {

  return (
    <section className="bestseller">
    <h3>BEST SELLER</h3>
    <div className='best-menu'>
      <ul className='on'>수분케어</ul>
      <ul>민감/진정</ul>
      <ul>보습/장벽</ul>
    </div>
    <Best


      // install Swiper modules
      modules={[Pagination, A11y, Autoplay]}
      spaceBetween={10}
      slidesPerView={3}
      loop={false}
      pagination={{ clickable: true }}
      autoplay={{delay:3000, disableOnInteraction: false}}
      onSwiper={(swiper) => console.log(swiper)}
      onSlideChange={() => console.log('slide change')}
    >

    <div className="swiper-container best-swiper">
      <ul className="swiper-wrapper">
        <SwiperSlide>

            <div className="img-wrap">
              <img src={process.env.PUBLIC_URL+"/img/bluebooster.jpg"}/>
              <ul className="basket"><img src={process.env.PUBLIC_URL+"/img/basket.png"}/></ul>
            </div>

            <div className="text-wrap" href="#">
              <p className="name">다이브인 저분자 히알루론산 수딩 크림 100ml</p>
              <div className="text-price-wrap">
                <p className="price">19,200원</p>
                <p className="prev-price">24,000원</p>
              </div>
            </div>
        </SwiperSlide>

        <SwiperSlide>
            <div className="img-wrap">
              <img src={process.env.PUBLIC_URL+"/img/b-cream.jpg"}/>
              <ul className="basket"><img src={process.env.PUBLIC_URL+"/img/basket.png"}/></ul>
            </div>

            <div className="text-wrap" href="#">
              <p className="name">[2개묶음] 다이브인 저분자 히알루론산 크림 80ml</p>
              <div className="text-price-wrap">
                <p className="price">27,300원</p>
                <p className="prev-price">42,000원</p>
              </div>
            </div>
        </SwiperSlide>

        <SwiperSlide>
            <div className="img-wrap">
              <img src={process.env.PUBLIC_URL+"/img/blueserum.jpg" }/>
              <ul className="basket"><img src={process.env.PUBLIC_URL+"/img/basket.png"}/></ul>
            </div>

            <div className="text-wrap" href="#">
              <p className="name">[2개묶음] 다이브인 저분자 히알루론산 세럼 50ml</p>
              <div className="text-price-wrap">
                <p className="price">26,900원</p>
                <p className="prev-price">44,000원</p>
              </div>
            </div>

        </SwiperSlide>

        <SwiperSlide>
            <div className="img-wrap">
              <img src={process.env.PUBLIC_URL+"/img/greencream.jpg"}/>
              <ul className="basket"><img src={process.env.PUBLIC_URL+"/img/basket.png"}/></ul>
            </div>

            <div className="text-wrap" href="#">
              <p className="name">밸런스풀 시카 진정 크림 80ml</p>
              <div className="text-price-wrap">
                <p className="price">18,000원</p>
                <p className="prev-price">24,000원</p>
              </div>
            </div>

        </SwiperSlide>

        <SwiperSlide>
            <div className="img-wrap">
              <img src={process.env.PUBLIC_URL+"/img/mask.jpg"}/>
              <ul className="basket"><img src={process.env.PUBLIC_URL+"/img/basket.png" }/></ul>
            </div>

            <div className="text-wrap" href="#">
              <p className="name">다이브인 저분자 히알루론산 마스크팩 27ml 10매</p>
              <div className="text-price-wrap">
                <p className="price">18,000원</p>
                <p className="prev-price">30,000원</p>
              </div>
            </div>

        </SwiperSlide>

      </ul>
      </div>

    </Best>
  </section>

  );




};



const Best = styled(Swiper)`
cursor:pointer;
width: 100%;
top: 0 !important;
height: auto;
padding-bottom: 30px;
margin: 60px auto;
margin-top: 30px;




.swiper-wrapper {
  margin:0 auto;
  
  .swiper-slide {
      position: relative;

      .img-wrap {
          width: 100%;
          position: relative;

          img{width:100%};

          .basket {
              position: absolute;
              bottom: 15px;
              right: 10px;
              width: 50px;
              height: 50px;
              background: #eaeaea;
              opacity: 0.7;
              border-radius: 100%;
              overflow: hidden;

              img{
                width:100%;
                height:100%;
              }


          }
      }

      .text-wrap {
          font-size: 1rem;
          margin: 10px 0;

          .text-price-wrap {
              font-size: 1.2rem;

              p {
                  margin: 5px 0;
                  font-weight: 800;
              }

              .prev-price {
                  text-decoration: line-through;
                  color: #b3b3b3;
                  font-size: 0.9rem;
              }
          }


      }


  }


}




.swiper-pagination {
  bottom: 0px;

  .swiper-pagination-bullet-active{
    background:#eaeaea;
  }

  

 

}


`