// import Swiper core and required modules
import { Navigation, Pagination, A11y, Autoplay, EffectFade } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
import styled from 'styled-components'

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import "swiper/css/effect-fade";



export default () => {
  return (
    
    <Banner
      // install Swiper modules

      modules={[Navigation, Pagination, A11y, Autoplay,EffectFade]}
      effect={"fade"}
      spaceBetween={50}
      slidesPerView={1}
      loop={true}
      navigation
      pagination={{ clickable: true }}
      autoplay={{delay:3000, disableOnInteraction: false }}
      onSwiper={(swiper) => console.log(swiper)}
      onSlideChange={() => console.log('slide change')}
    >
      <SwiperSlide> <ul className="swiper-slide"><a href="#"><img src={process.env.PUBLIC_URL+"/img/banner4.jpg"}/></a></ul></SwiperSlide>
      <SwiperSlide><ul className="swiper-slide"><a href="#"><img src={process.env.PUBLIC_URL+"/img/banner3.jpg"}/></a></ul></SwiperSlide>
      <SwiperSlide><ul className="swiper-slide"><a href="#"><img src={process.env.PUBLIC_URL+"/img/banner1.jpg"}/></a></ul></SwiperSlide>
      <SwiperSlide><ul className="swiper-slide"><a href="#"><img src={process.env.PUBLIC_URL+"/img/banner2.jpg"}/></a></ul></SwiperSlide>
      <SwiperSlide><ul className="swiper-slide"><a href="#">
        <img src={process.env.PUBLIC_URL+"/img/banner5.jpg"}/>
        </a></ul></SwiperSlide>

      
   
    </Banner>

  );

};



const Banner = styled(Swiper)`
height: 800px;
position: relative;
top: 75px;
left: 0;

.swiper-slide {
    width: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position:relative;

    img {
        width: 100%;
    }
}

.swiper-button-prev {
  background-image: url(${process.env.PUBLIC_URL +"/img/prevarrow.png"});
  background-repeat: no-repeat;
    opacity: 0.6;
    width:25px;
    height:50px;
    background-size:100%;
    position:absolute;
    top:50%;
    left:10px;


}

.swiper-button-prev::after {

  font-size:0;
}

.swiper-button-next {
    background-image: url(${process.env.PUBLIC_URL +"/img/nextarrow.png"});
    background-repeat: no-repeat;
    opacity: 0.6;
    width:25px;
    height:50px;
    background-size:100%;
    position:absolute;
    top:50%;
    right:10px;
    
}

.swiper-button-next::after {
  font-size:0;
}

.swiper-pagination-bullet-active {
    background: #eaeaea;
}
`